.logo {
	display: flex;
	/* .logo__img */
	&__img {
		width: 30px;
		height: 30px;
		margin-right: 10px;
	}
	/* .logo__text-box */
	&__text-box {
		font-size: 15px;
		text-transform: uppercase;
		font-weight: 700;
		line-height: 15px;
		color: $color-light-1;
	}
	/* .logo__text */
	&__text {
		
		/* .logo__text_up */
		&_up {
			font-weight: 400;
		}
		/* .logo__text_down */
		&_down {
			
		}
	}
}
