.btn{
	display: inline-block;
	font-size: 1.5rem;
	text-transform: uppercase;
	padding: 2rem 2.8rem;
	border-radius: 3px;
    transition: color 1s;
    position: relative;
	font-family: Roboto;
	font-weight: 400;
	text-transform: uppercase;
	

	/* .btn__content */
	&__content {
		position: relative;
		z-index: 1;
	}
	/* .btn_primary */
	&_primary{
		background: $color-primary;
		color: $color-light-1;
	}
	/* .btn_dark */
	&_dark {
		background: $color-dark-1;
		color: $color-light-1;
	}
	/* .btn_light */
	&_light {
		background: $color-light-1;
		color: $color-dark-1;
	}


	&:hover{
		color: $color-light-1;
	}
	
    //////// Hover effects::before
	
    &::before{
    	content: "";
    	position: absolute;
    	top: 0;
		left: 0;
		transform: scaleY(0);
		height: 100%;
       	width: 5px;
		background: $color-secondary;
		z-index: 1;
		transition: transform .3s,
                    width .4s cubic-bezier(1,0,0,1) .3s,
                    background-color .1s;

    }
    &:hover::before {
		transform: scale(1);
		width: 100%;
	}
	
	

}