.nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: $color-light-1;
	padding: 5.2rem 0 0;
	margin-bottom: 16.9rem;
	
	/* .nav__list */
	&__list {
		display: flex;

		@media only screen and (max-width: $bp-medium){
			top: 0;
			left: -100%;
			position: fixed;
			z-index: 10;
			width: 100%;
			height: 100vh;
			background: green;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			transition: 0.2s ease-in-out;
		}
	}
	/* .nav__item */
	&__item {
		
		&:not(:last-child){
			margin-right: 4.9rem;
			@media only screen and (max-width: $bp-medium){
				margin-right: 0rem;
			}
		}
	}
	/* .nav__link */
	&__link {
		color: inherit;
		font-size: 1.5rem;
		font-weight: 400;
		line-height: 2.5rem;
		@media only screen and (max-width: $bp-medium){
			font-size: 3.5rem;
			margin-bottom: 3.5rem;
		}
		/* .nav__link_act */
		&_act {
			color: $color-dark-1;
		}
		&:hover{
			color: $color-dark-1;
		}
	}

	/* .nav__sign-up */
	&__sign-up{
		position: relative;
		color: inherit;
		font-size: 1.2rem;
		font-weight: 400;
		line-height: 2.5rem;
		border: 1px solid $color-light-1;
		border-radius: 5px;
		padding: 0.6rem 2.1rem;

		@media only screen and (max-width: $bp-medium){
			position: fixed;
			top: 3.5rem;
			left: 3.5rem;
			display: none;
		}
		&-text{
			position: relative;
			z-index: 2;
		}
		&::before{
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			height: 3px;
			width: 100%;
			transform: scaleX(0);
			background: $color-light-1;
			transition: transform .2s,
                    	height .2s ease .2s;
		}

		&:hover{
			color: $color-dark-1;
		}
		&:hover::before{
			transform: scale(1);
			height: 100%;
		}
	}


	/* .nav__chk */
	&__chk {
		display: none;
		position: absolute;
		visibility: hidden;
		top: 5.5rem;
		right: 1.5rem;
		width: 5rem;
		height: 5rem;
		background: $color-light-1;
		border-radius: 50%;
		&:checked ~ .nav__list{
			left: 0;
		}
		&:checked ~ .nav__sign-up{
			display: block;
		}
		&:checked ~ .nav__btn-box .nav__btn{
			
			transform: translate(-50%,-50%) rotate(225deg);
			&::before,
			&::after{
				top: 0;
				transform: rotate(90deg);
			}
		}
	}
	/* .nav__btn-box */
	&__btn-box {
		display: none;
		cursor: pointer;
		position: absolute;
		top: 5.5rem;
		transform: translateY(-50%);
		right: 3rem;
		width: 5rem;
		height: 5rem;
		background: $color-light-1;
		border-radius: 50%;
		z-index: 100;
		@media only screen and (max-width: $bp-medium){
			display: block;
			position: fixed;
		}
		&:hover .nav__btn::before{
			top: -9px;
		}
		&:hover .nav__btn::after{
			top: 9px;
		}
	}
	/* .nav__btn */
	&__btn {
		position: relative;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		width: 60%;
		height: 3px;
		background: #000;
		transition: transform .3s;
		&::before,
		&::after{
			content: "";
			position: absolute;
			top: -7px;
			width: 100%;
			height: inherit;
			background: #000;
			transition: top .1s,
			   			transform .3s;

		}

		&::after{
			top: 7px;
		}

	}


}








