// nav__btn
#nav__btn{
	display: none;
	cursor: pointer;
	position: absolute;
	top: 4.5rem;
	transform: translateY(-50%);
	right: 3rem;
	width: 5rem;
	height: 5rem;
	background: $color-light-1;
	border-radius: 50%;
	z-index: 100;
	@media only screen and (max-width: $bp-small){
		display: block;
		position: fixed;
	}
	// nav__btn_line
	&_line{
		position: relative;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		width: 60%;
		height: 3px;
		background: #000;
		transition: transform .3s;
		&::before,
		&::after{
			content: "";
			position: absolute;
			top: -7px;
			width: 100%;
			height: inherit;
			background: #000;
			transition: top .1s,
			   			transform .3s;

		}

		&::after{
			top: 7px;
		}
	}

	&:hover #nav__btn_line::before{
		top: -9px;
	}
	&:hover #nav__btn_line::after{
		top: 9px;
	}
	
}
