.footer {
	background: $color-dark-1;
	color: $color-light-1;
	padding: 5rem 0;
	/* .footer__container */
	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	/* .footer__list */
	&__list {
		display: flex;
	}
	/* .footer__item */
	&__item {
		margin: 1.5rem;
	}
	/* .footer__link */
	&__link {
		color: inherit;
		font-size: 2rem;
		font-weight: 400;
		line-height: 2.5rem;

	}


	@media only screen and (max-width: $bp-medium){
		&__container {
			flex-direction: column;

		}
		
	}
}

