.heading-primary{
	
	color: $color-light-1;
	font-size: 5.5rem;
	font-weight: bold;
	line-height: 6.5rem;
	text-transform: uppercase;
	


	@media only screen and(max-width: $bp-medium){
      font-size: 3rem;

    }
}
.heading-secondary{
	
	color: $color-dark-1;
	font-family: "Raleway - Semi Bold";
	font-size: 4.5rem;
	font-weight: 400;
	/* .heading-secondary_light */
	&_light {
		color: $color-light-1;
	}

}

.heading-tertiary{
	color: $color-dark-1;
	font-family: "Raleway - Semi Bold";
	font-size: 2rem;
	font-weight: 400;
	line-height: 2.5rem;
}

.paragraph{
	font-size: 16px;
	font-weight: 400;
	letter-spacing: 0.36px;
	opacity: 0.7;

	/* .paragraph_dark */
	&_dark {
		color: $color-dark-1;
		
	}
	
	/* .paragraph_light */
	&_light {
		color: $color-light-1;
	}

	/* .paragraph_big */
	&_big {
		
		font-size: 1.8rem;
		font-weight: 700;
		line-height: 2.5rem;
	}
}

