
.header {
	background: url('../img/header_bg/header_bg.png') no-repeat center center/cover;
	min-height: 100vh;
	width: 100%;
	z-index: 1;
	position: relative;
	&::before{
	    content: "";
	    position: absolute;
	    width: 100%;
	    height: 100%;
	    background: black;
	    z-index: -1;
        background-image: linear-gradient(-53deg, #00b8d4 0%, #1de9b6 100%);
        opacity: 0.6;
	}
	

	/* .header__showcase */
	&__showcase {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		margin: 0 -2rem;
	}

	/* .header__my-bottom */
	&__my-bottom {
		margin-bottom: 9.9rem;
	}


	/* .header__text */
	&__text {
		width: calc(100% / 12 * 5.5 - 4rem);
		margin: 0 2rem;
	}
	/* .header__video */
	&__video {
		height: 500px;
		width: calc(100% / 12 * 5.5 - 4rem);
		background: lightblue;
		margin: 0 2rem;
	}

	@media only screen and (max-width: $bp-medium){

		&__text,
		&__video {
			width: calc(100% / 12 * 11);
		}
	
	}



}
