.cards {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-left: -1.5rem;
	margin-right: -1.5rem;
	/* .cards__cards-box */
	&__cards-box {
		//////////////////////////////////
		// It looks like a bug in IE with calc
		// So instead of using "calc(100% / 12 * 3 - 3rem);"
		// We use: "width: calc(100% / 12 * 2.9 - 3rem);"
		// Bug is explained here: 
		// https://stackoverflow.com/questions/21942183/multiline-flexbox-in-ie11-calculating-widths-incorrectly/21950351#21950351
		width: calc(100% / 12 * 3.9 - 3rem);
		margin-left: 1.5rem;
		margin-right: 1.5rem;
		text-align: center;
		

	}
	/* .cards__img-box */
	&__img-box {
	}
	/* .cards__img */
	&__img {
		height: 6.8rem;
		margin-bottom: 4.5rem;

	}
	@media only screen and (max-width: $bp-medium){

		&__cards-box{
			//////////////////////////////////
			// It looks like a bug in IE with calc
			// So instead of using "calc(100% / 12 * 6 - 3rem);"
			// We use: "width: calc(100% / 12 * 5.5 - 3rem);"
			// Bug is explained here: 
			// https://stackoverflow.com/questions/21942183/multiline-flexbox-in-ie11-calculating-widths-incorrectly/21950351#21950351 
			width: calc(100% / 12 * 8 - 3rem);
			margin-bottom: 5rem;
			position: relative;
			padding-bottom: 5rem;
			&::before{
				content: "";
				position: absolute;
				width: 50%;
				height: 2px;
				background: lightgray;
				left: 50%;
				transform: translateX(-50%);
				bottom: 0;
			}
		}
	}
}



