.features {
	text-align: center;
	

	/* .features__my-bottom */
	&__my-bottom {
		margin-bottom: 10rem;
	}
	/* .features__cards-my-bottom */
	&__cards-my-bottom {
		margin-bottom: 7.8rem;
	}

}

.statistics {
	display: flex;
	justify-content:center;
	flex-wrap: wrap;
	/* .statistics__box */
	&__box {
		height: 150px;
		width: calc(100% / 12 * 3);
		display: flex;
		align-items: center;
		justify-content: center;
		/* .statistics__box_first */
		&_first {
			background: #80d8ff;
		}
		/* .statistics__box_second */
		&_second {
			background: #82b1ff;
		}
		/* .statistics__box_third */
		&_third {
			background: #b388ff;
		}
		/* .statistics__box_fourth */
		&_fourth {
			background: #ea80fc;
		}
	}
	/* .statistics__img-box */
	&__img-box {
		width: 60px;
		height: 60px;
		background: $color-light-1;
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 2rem;
	}
	/* .statistics__img */
	&__img {
		
	}
	/* .statistics__text */
	&__text {
		color: $color-light-1;
		font-size: 2rem;
		font-weight: 400;
		line-height: 2.5rem;
	}
	
	@media only screen and (max-width: $bp-large){
		&__box{
			width: calc(100% / 12 * 6);
		}
	}

	@media only screen and (max-width: $bp-medium){
		&__box{
			width: calc(100% / 12 * 11);
		}
	}
}

.services {
	padding-top: 10rem;
	padding-bottom: 10rem;
	
	/* .services__info */
	&__info {
	}
}



.testimonials {
	padding: 8.7rem 0;
	background: url('../img/testimonials/testimonials_bg.png') no-repeat center center/cover;
	min-height: 56.4rem;
	width: 100%;
	z-index: 1;
	position: relative;
	
	&::before{
		top: 0;
	    content: "";
	    position: absolute;
	    width: 100%;
	    height: 100%;
	    background: black;
	    z-index: -1;
        background-image: linear-gradient(-53deg, #00b8d4 0%, #1de9b6 100%);
        opacity: 0.9;
	}

	/* .testimonials__container */
	&__container {
		display: flex;
		flex-wrap: wrap;
	}


	/* .testimonials__main */
	&__main {
		width: calc(50%);
	}

	/* .testimonials__header */
	&__header {
		max-width: 44.4rem;

	}

	/* .testimonials__subtitle */
	&__subtitle {
		max-width: 51rem;
		margin-bottom: 7.8rem
	}


	/* .testimonials__persons */
	&__persons {
		width: calc(50%);
	}
	/* .testimonials__person */
	&__person {
		display: flex;
		align-items: center;
	}

	/* .testimonials__photo */
	&__photo {
		width: 6rem;
		height: 100%;
		margin-right: 5rem;
	}
	/* .testimonials__text */
	&__text {
		background: $color-light-1;
		padding: 3rem;
		box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
		border-radius: 5px;

	}
	/* .testimonials__name */
	&__name {

		color: $color-dark-1;
		font-size: 1.8rem;
		font-weight: 700;
		line-height: 2.5rem;
	}

	@media only screen and (max-width: $bp-medium){
		&__container {
			justify-content: center;
		}
		&__main {
			width: calc(99%);
			text-align: center;
			margin-bottom: 3rem;
		}
		/* .testimonials__header */
		&__header {
			margin-left:  auto;
			margin-right: auto;
		}
		/* .testimonials__subtitle */
		&__subtitle {
			margin-left:  auto;
			margin-right: auto;
		}
		/* .testimonials__persons */
		&__persons {
			width: calc(99%);
		}
	}
}

.team {
	padding-bottom: 10rem;
	padding-top: 10rem;
	/* .team__gallery */
	&__gallery {
		display: flex;
		justify-content: center;
	}
	/* .team__person */
	&__person {
		position: relative;
	}

	/* .team__photo-box */
	&__photo-box {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 12rem;
		height: 12rem;
		&:hover ~ .team__text{
			opacity: 1;
			top: 110%;
		}
	}

	/* .team__photo */
	&__photo {
		width: 8rem;
		height: 8rem;
		margin-left: 2rem;
		margin-right: 2rem;
		opacity: 0.6;
		transition: 0.5s;
		&:hover {
			width: 12rem;
			height: 12rem;
			opacity: 1;
		}
	}

	/* .team__text */
	&__text {
		text-align: center;
		opacity: 0;
		position: absolute;
		width: 50rem;
		top: 150%;
		left: 50%;
		transform: translateX(-50%);
		transition: 0.5s;
	}
	@media only screen and (max-width: $bp-medium){
		&__gallery{
			flex-direction: column;
		}
		&__photo-box{
			margin: 0 auto;
		}
		&__photo{
			opacity: 1;
		}
		&__text{
			position: relative;
			opacity: 1;
		}
		&__chk{

		}

	}
}

